<template>
  <div class="testimonials">
    <LoadingSpinner v-if="loading" />
    <div class="inner">
      <h1 class="whiteTheme">Reviews</h1>
      <div class="border"></div>
      <v-row no-gutters>
        <v-col
          class="pa-3"
          v-for="review in reviews"
          :key="review._id">
          <div class="testimonial">
            <img
              :src="`${review.UserImage}`"
              alt="" />
            <div class="name">{{ review.UserName }}</div>
            <p class="grey--text">
              {{
                review.Review.length >= 330
                  ? review.Review.substring(0, 330) + "..."
                  : review.Review
              }}
            </p>
            <v-rating
              v-model="rating"
              background-color="purple lighten-3"
              color="green"
              medium
              readonly></v-rating>
          </div>
        </v-col>
      </v-row>
    </div>
  </div>
</template>

<script>
  import API from "@/reviewapi";
  import LoadingSpinner from "../components/LoadingSpinner.vue";
  export default {
    name: "ReviewView",
    data() {
      return {
        reviews: [],
        rating: 4,
        loading: false,
      };
    },
    components: {
      LoadingSpinner,
    },
    async created() {
      this.loading = true;
      this.reviews = await API.getAllPost();
      this.loading = false;
    },
  };
</script>

